import React, {useEffect} from 'react'
import styled from 'styled-components'
import '../assets/css/0_basic.scss'
import AOS from 'aos';
import "aos/dist/aos.css";  
import sohyun from '../assets/img/exhibit_main/sohyun.jpg'
import hasom from '../assets/img/exhibit_main/hasom.jpg'
import cheolhan from '../assets/img/exhibit_main/cheolhan.jpg'

const Div = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    Text-align: center;
    margin-bottom: 72px;
`;

// const LRiv = styled.div`
//     display: flex;
//     flex-direction: row;
//     justify-content: center;
//     margin-bottom: 36px;
// `

const Btitle = styled.div`
    font-size: 26px;
    margin-bottom: 48px;
`;

const Mtitle = styled.div`
    font-size: 22px;
    margin-bottom: 24px;
`;

const Stitle = styled.div`
    font-size: 18px;
    margin-bottom: 8px;
`;

const Text = styled.div`
    font-size: 14px;
    margin-bottom: 12px;    
    text-align: center;
`;

// const Text1 = styled(Text)`
//     margin-bottom: 40px;
// `

// const Img = styled.img`
//     width: 309px;
//     height: 400px;
//     object-fit: scale-down;
//     margin: 0px 32px 0px; 32px ;
// `;

    const Container = styled.div`
        display: grid;
        grid-template-columns: 309px 310px 309px;
        column-gap: 24px;
    `

    const Box = styled.div`
        display: flex;
        flex-direction: column;
        align-items: center;
    `

    const Img = styled.img`
        width: 100%;
        height: 400px;
        object-fit: cover;
    `;

    const Button = styled.button`
    text-align: center;
    background-color: #efafed;
    border: 1px solid #ffffff;
    margin-top: 12px;
    `;

function Content1() {

    useEffect(() => {
        AOS.init();
    }, []);  

    return (
        <>
            <Div>
                <Btitle>Personal Color List |</Btitle>
                <Stitle>- Color Blue      | 09/03~4  , </Stitle>
                <Stitle>- Color Yellow     | Comming Soon</Stitle>
                <Stitle>- Color Ash - Blue     | Comming Soon</Stitle>
            </Div>
            <Div>
                <Mtitle>    ?</Mtitle>
                <Text>   ,      .</Text>
                <Text>'  ,     '</Text>
                <Text>',          '</Text>
                <Text>'  ?      ?'</Text>
            </Div>
            <Container>
                <Box>
                    <Img src={sohyun}></Img>
                    <Button>First Color_Blue 
                    <br/>
                    <br/>   </Button>
                </Box>
                <Box>
                <Img src={hasom}></Img>        
                    <Button>Second Color_Yellow
                    <br/>
                    <br/>   </Button>
                </Box> 
                <Box>
                    <Img src={cheolhan} ></Img>
                    <Button>Third Color_Gray
                    <br/>
                    <br/>   </Button>
                </Box>
            </Container>
        </>
    )
}

export default Content1;