import React, { useState } from 'react'
import styled from 'styled-components'
import SelectExhibit from './1_SelectExhibit';
import SelectDay from './2_SelectDay';
import SelectTime from './3_SelectTime';
import '../assets/css/0_basic.scss'

    const Div = styled.div`
        display: flex;
        flex-direction: column;
        align-items: center;
    `

    const Button = styled.button`
        width: 328px;
        height: 56px;
        background: #DBFD5C;
        box-shadow: 0px -1px 4px rgba(255, 255, 255, 0.12), 0px 2px 16px rgba(0, 0, 0, 0.24);
        backdrop-filter: blur(4px);
        border-radius: 12px;
        margin-top 12px;
        font-size: 16px;
        line-height: 22px;
        color: black;
    `;

function Reservation() {

    const [exhibit, setExhibit] = useState(' ')
    const [day, setDay] = useState(' ')
    const exhibit_day = [
        {exhibit: ' '},
        {exhibit: '1', first:'9 3', second:'9 4'},
        {exhibit: '2', first:'9 17', second:'9 18'},
        {exhibit: '3', first:'9 24', second:'9 25'},
    ]
    const [time, setTime] = useState(' ')
    const exhibit_time = [
        {day: ' '},
        {day: '9 3', first:'15', second:'17', third: '19'},
        {day: '9 4', first:'15', second:'17', third: '19'},
        {day: '9 17', first:'14', second:'16', third: '18', fourth:'20'},
        {day: '9 18', first:'14', second:'16', third: '18', fourth:'20'},
        {day: '9 24', first:'14', second:'15 30', third: '17', fourth:'18 30', fifth: '20'},
        {day: '9 25', first:'14', second:'15 30', third: '17', fourth:'18 30', fifth: '20'},
    ]

    return ( 
    <Div>
        <SelectExhibit exhibit={exhibit} onChangeNumber = {(number1) => {
            setExhibit(number1)
            setDay(' ')
            setTime(' ')
        }}></SelectExhibit>
        <SelectDay exhibit={exhibit} day={day} exhibit_day={exhibit_day} onChangeNumber = {(number2) => {
            setDay(number2)
            setTime(' ')
        }}></SelectDay>
        <SelectTime exhibit={exhibit} day={day} time={time} exhibit_time={exhibit_time} onChangeNumber = {(number3) => {setTime(number3)}}></SelectTime>
        <Button> </Button>
    </Div>
    );
}

export default Reservation;